#works{
    margin: 0 auto;
    min-height: calc(100vh - 5rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 0 1rem;
}
.worksTitle{
    margin: 1rem 0;
    font-size: 3rem;
}
.WorksDesc{
    font-weight: 300;
    font-size: 1rem;
}
.worksImgs{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}
.worksImg{
    object-fit: cover;
    height: 20rem;
    width: 20rem;
    margin: 0.5rem;
}
.worksbtn{
    margin: 3rem 0;
    padding: 0.5rem 2.5rem;
    background-color: #fff;
    border: none;
    border-radius: 2rem;
    font-size: 1rem;
    cursor: pointer;
}
@media  screen and (max-width:585px) {
    .worksImg{
        height: 48vw;
    }
}